.Timeline{
  min-width: 1000vw;
  text-align: center;

  position: absolute;
  top:    200px;
  left:   0;
  bottom: 0;
}

.Timeline ol {
	position:   relative;
	display:    block;
	margin:     100px;
	height:     4px;
	background: #333;
}
.Timeline ol::before,
.Timeline ol::after {
	content: "";
	position: absolute;
	top: -8px;
	display: block;
	width: 0;
	height: 0;
  border-radius: 10px;
	border: 10px solid #333;
}
.Timeline ol::before {
	left: -5px;
}
.Timeline ol::after {
	right: -10px;
	border: 10px solid transparent;
	border-right: 0;
	border-left: 20px solid #333;
  border-radius: 3px;
}

/* ---- Timeline elements ---- */
ol li {
	position: relative;
	display:  inline-block;
	float:    left;
	width:    500px;
  height:   50px;
}
ol li .date, ol li .event {
  transition: 0.3s ease all;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  color: #000000;
  text-transform: capitalize;
}
ol li .date{
  font-size: 1.8rem;
  white-space: nowrap;
  top: -5rem;
}
ol li .event{
  font-size: 2rem;
  top: 3rem;
}
ol li .point {
	content: "";
	top: -4px;

  left: 50%;
  transform: translateX(-50%);

	display: block;
	width: 6px;
	height: 6px;
	border: 4px solid #333;
	border-radius: 50%;
	background: #fff;
  position: absolute;
  transition: 0.3s ease all;
}



ol li .description {
  visibility: hidden;
  opacity: 0;
  transition: 0.3s ease all;

  position: fixed;
  bottom:   2rem;
  right:    2rem;
  z-index:  1;
  width: 600px;

  padding: 5px 10px;
  border: solid 1px #424542;
  box-shadow:  1px  1px #e7dfe7,
              -1px -1px #e7dfe7,
               1px -1px #e7dfe7,
              -1px  1px #e7dfe7,
               0px -2px #9c9a9c,
              -2px  0px #7b757b,
               0px  2px #424542;

  background: #04009d;
  background: linear-gradient(to bottom,  #04009d 0%,#06004d 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#04009d', endColorstr='#06004d',GradientType=0 );

  -webkit-border-radius: 7px;
     -moz-border-radius: 7px;
      border-radius: 7px;

  /* text attributes */
  color: #eff1ff;
  text-align: left;
  text-shadow: 2px 2px #212421,
               1px 1px #212021;
  font-family: Verdana, sans-serif;
  font-size: 20px;
  font-weight: normal;
}

/* ---- Show description when event is active ---- */
ol li.active > *{
  color: #FFFFF0;
  text-shadow: 1px 1px 3px #000;
}
ol li.active .description {
  visibility: visible;
  opacity: 1;
}
ol li.active .point {
  width:      15px;
  height:     15px;
  border:     4px solid #04009d;
  background: #04009d;
  top:       -9px;
}
