.App { }

.App-header {
  background-color: #FFFFF0;
  min-height: 50px;
  font-size: calc(10px + 2vmin);
  color: white;

  box-shadow: 1px 1px 5px #333;

  position: fixed;
  width: 100%;
  top: 0;
}

.App-header img{
  margin: 5px 15px;
  max-height: 50px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
